import AbstractDomain from './AbstractDomain'
import Person from './Person'
import Project from './Project'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

export enum State {
  REQUESTED,
  APPROVED,
  DENIED,
  ARCHIVED,
}

@Resource.Name('projectParticipations')
export default class ProjectParticipation extends AbstractDomain<ProjectParticipation> {
  @JsonProperty() public personId: string
  @JsonProperty() public person?: Person
  @JsonProperty() public projectId: string
  @JsonProperty() public project?: Project
  @JsonProperty() public state: State
  @JsonProperty() public capacity: number
  @JsonProperty() public dateStart: Date
  @JsonProperty() public dateEnd: Date
  @JsonProperty() public fromParticipationId?: string
  @JsonProperty() public fromParticipation?: ProjectParticipation
}